@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500;700&display=swap);
header {
    width: 100%;
    height: 155px;
    background-color: hsl(180, 29%, 50%);
    background-size: 100% 100%;
    background-image: url('https://raw.githubusercontent.com/degranda/job-list/61c41ce1a41d013450ec10d86c235ec47e3d0261/images/bg-header-desktop.svg');
}
@media screen and (max-width: 600px) {
    header {
        background-image: url('https://raw.githubusercontent.com/degranda/job-list/61c41ce1a41d013450ec10d86c235ec47e3d0261/images/bg-header-mobile.svg');
    }
}
button {
    border: none;
    border-radius: 5px;
    outline: none;
    color: hsl(180, 29%, 50%);
    background-color: hsl(180, 31%, 95%);
    font-weight: bold;
    padding: 10px;
    margin: 0 6px;
    cursor: pointer;
}

button:hover, button:focus {
    background-color: hsl(180, 29%, 50%);
    color: hsl(180, 31%, 95%);
}
.jobcard {
    border-radius: 5px;
    width: 85%;
    height: 150px;
    background-color: white;
    margin: 15px 0;
    border-left: 5px solid hsl(180, 29%, 50%);
    box-shadow: 1px 5px 20px -5px black;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 35px;
}

.jobcard img {
    width: 100px;
}

.company {
    display: flex;
    align-items: center;
}

.name {
    color: hsl(180, 29%, 50%);
    font-weight: bold;
}

.description {
    margin-left: 20px;
}

.position {
    color: hsl(180, 14%, 20%);
    font-weight: bold;
    font-size: 20px;
    margin: 15px 0;
    cursor: pointer;
}

.new {
    background-color: hsl(180, 29%, 50%);
    color: white;
    font-weight: bold;
    font-size: 10px;
    padding: 6px 8px;
    border-radius: 100px;
    margin: 0 5px 0 15px;
}

.new div {
    top: 1px;
}

.featured {
    background-color: hsl(180, 14%, 20%);
    color: white;
    font-weight: bold;
    font-size: 10px;
    padding: 6px 8px;
    border-radius: 100px;
}

.featured div {
    top: 1px;
}

.filters {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-wrap: wrap;
}

.list {
    display: flex;
    align-items: center;
}

.posted {
    list-style: none;
}

.contract {
    margin: 0 35px;
}

.list li {
    color: hsl(180, 8%, 52%);
}

.position:hover {
    color: hsl(180, 29%, 50%);
}

@media screen and (max-width: 600px) {
    .jobcard {
        flex-direction: column;
        height: 300px;
        padding: 0;
        margin-top: 50px;
    }
    .company-1 {
        flex-direction: column;
        align-items: flex-start;
        top: -15px;
    }
    .jobcard img {
        width: 75px;
        top: -15px;
    }
    .description {
        margin: 0;
    }
    .filters {
        width: 90%;
        justify-content: flex-start;
        border-top: 1px solid black;
    }
}
@media screen and (max-width: 375px) {
    .jobcard {
        width: 95%;
    }
}
@media screen and (max-width: 320px) {
    .description {
        left: 0;
    }
    .jobcard img {
        left: 5px;
    }
}
.filtersList {
    border-radius: 5px;
    width: 85%;
    background-color: white;
    box-shadow: 1px 5px 20px -5px black;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 30px;
    animation: showList 500ms forwards;
    position: absolute;
    top: -120px;
}

.allFilters {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.filterButton {
    background-color: hsl(180, 29%, 50%);
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    top: 3px;
    right: 15px;
    height: 34px;
}

.clear {
    background: none;
    color: hsl(180, 8%, 52%);
    font-size: 16px;
    font-weight: normal;
}

.clear:hover, .clear:focus {
    background: none;
    color: hsl(180, 29%, 50%);
    text-decoration: underline;
}

.filterButton:hover, .filterButton:focus {
    background-color: hsl(180, 14%, 20%);
}

@keyframes showList {
    from {
        min-height: 0;
    } to {
        min-height: 75px;
    }
}

@media screen and (max-width: 600px) {
    .filtersList {
        padding: 0;
        padding: 10px 5px;
        top: -165px;
    }
    .clear {
        font-weight: bold;
    }
}
main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin-top: 80px;
}
a {
    color: hsl(180, 8%, 52%);
    text-decoration: none;
}

a:hover {
    color: hsl(180, 29%, 50%);
    text-decoration: underline;
}

.ErrorTitle {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    top: -75px;
}
@media screen and (max-width: 600px) {
    .ErrorTitle {
        flex-direction: column;
        text-align: center;
    }
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Spartan', sans-serif;
  position: relative;
}

body {
  width: 100vw;
  background-color: hsl(180, 52%, 96%);
}
